%size {
  @each $item, $size in get("size") {
    &-#{$item}, &.#{$item} {
      box-sizing: border-box;
      min-width: size(map_get($size, height));
      height: size(map_get($size, height));
      font-size: size(get("size."+ $item + ".font-size"));
      line-height: size(map_get($size, height) - 2px);
      // display: inline-flex;
      // align-items: center;
    }
  }
}

%padding-button {
  @each $item, $size in get("element.button.size") {
    &.#{$item}, &-#{$item} {
      padding: 0 size(map_get($size, padding) - 1px);
    }
  }
}

%padding-input {
  @each $item, $size in get("element.input.size") {
    &.#{$item}, &-#{$item} {
      padding: 0 size(map_get($size, padding) - 1px);
    }
  }
}
