select {
  @extend %padding-input-medium, %input;

  padding-right: get("element.select.size.large.padding-right");
  cursor: pointer;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-position: right size(5px) top 50%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);

  @each $size, $value in get("element.select.size") {
    &.#{$size} {
      @extend %padding-input-#{"" + $size};

      padding-right: get("element.select.size."+$size+".padding-right");
    }
  }
}
