:root {
  @each $name, $hex-value in $colors {
    --#{$name}: #{$hex-value};

    @if (not str-index(quote($name), "ghost")) {
      --#{$name}-rgb: #{hex-to-rgb($hex-value, $name)};
    }
  }
}
